import axios, { AxiosRequestConfig } from "axios";
import moment from "moment";

//fetcher fuction
// fetch promise 

const Base_URL = `https://dev.sfoc.kro.kr`;

export function storelocalToken(token: any) {
  localStorage.setItem("accessToken", token);
}

//header   200    
//400  body code  
//code: 101 => login/reissue (api)
//code: 102,103 => login page

export function fetchUserApi(token: string) {
  return axios
    .get(`${Base_URL}/api/login/loginInfo`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    .then((response) => {
      console.log(response.data);
      return response.data;
    })
    .catch((error) => {
      return handleError(error);
    });
}

function handleError(error: any) {
  if (error.response && error.response.status === 400) {
    // 400   :   
    switch (error.response.code) {
      case 101:
        return axios
          .get(`${Base_URL}/api/login/reissue`, {})
          .then((response) => {
            storelocalToken(response);
          });
      case 102:
        //  
        break;
      case 103:
        return axios
          .get(`${Base_URL}/api/login/reissue`, {})
          .then((response) => {
            console.log("   =>  ");
            console.log(error);
            console.log(response);
            storelocalToken(response);
          });
      case 104:
        //  
        break;
      case 105:
        //  
        break;
      default:
        break;
    }
  } else {
    //      
    console.error("Error handling request:", error);
    throw error; //    
  }
}

