import { useState } from "react";
import styled from "styled-components";

const AlarmBubble = styled.div`
  position: absolute;
  z-index: 20;
  top: 90px;
  left: 120px;
  width: 400px;
  background: #d9d9d9;
  border-radius: 15px;
  padding: 8px 10px;

  &::after {
    content: "";
    position: absolute;
    top: -10px;
    left: 10%;
    transform: translateX(-50%);
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #d9d9d9;
  }
`;

const AlarmContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-radius: 20px;
  padding: 2px 8px;
  margin: 5px 0;
  & p {
    font-size: 15px;
  }
`;

const DeleteBtn = styled.button`
  border: 0;
  background-color: transparent;
  color: gray;
`;

function Alarm() {
  const [alarms, setAlarms] = useState([
    { id: 1, team: "xxx", user: "ooo" },
    { id: 2, team: "yyy", user: "ppp" },
    { id: 3, team: "ooo", user: "xxx" },
  ]);

  const handleDelete = (id: number) => {
    //           .
    const updatedAlarms = alarms.filter((alarm) => alarm.id !== id);
    setAlarms(updatedAlarms);
  };
  return (
    <div>
      {alarms.length === 0 ? (
        <AlarmBubble style={{ textAlign: "center" }}>
           .
        </AlarmBubble>
      ) : (
        <AlarmBubble>
          {alarms.map((alarm) => (
            <AlarmContainer key={alarm.id}>
              <p>{`${alarm.team} ${alarm.user}()  `}</p>
              <DeleteBtn onClick={() => handleDelete(alarm.id)}>
                
              </DeleteBtn>
            </AlarmContainer>
          ))}
        </AlarmBubble>
      )}
    </div>
  );
}

export default Alarm;
